import styled from 'styled-components';
import Movie from './Movie';

const Grid = styled.div`
  margin: 2vh 2.5vw;
  border: 1px solid black;
  display: grid;
`;

const MovieGrid = ({ movies }) => (
  <Grid>
    {movies.map(movie => (
      <Movie movie={movie} />
    ))}
  </Grid>
);

export default MovieGrid;
